@font-face {
    font-family: 'Material Symbols Outlined';
    font-style: normal;
    src: url(./MaterialSymbolsRounded[FILL\,GRAD\,opsz\,wght].ttf) format('truetype');
  }

.material-symbols-outlined {
font-family: 'Material Symbols Outlined';
font-weight: normal;
font-style: normal;
font-size: 24px;  /* Preferred icon size */
display: inline-block;
line-height: 1;
text-transform: none;
letter-spacing: normal;
word-wrap: normal;
white-space: nowrap;
direction: ltr;
font-variation-settings:
  'FILL' 1,
  'wght' 700,
  'GRAD' 200,
  'opsz' 48
}


  